.advantages {
  background-color: $color-bg-section;
  padding-bottom: 97px;

  &__list {
    list-style: none;
  }

  &__element {
    &--img {
      margin-top: 10px;
    }

    &--data {
      color: $darkRed;
      font-family: $font-family-secondary;
      font-size: 36px;
      font-weight: 400;
      line-height: 41px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      padding-top: 16px;
    }

    &--description {
      color: $color-black;
      font-family: $font-family-primary;
      font-size: $font-size-primary-mobile;
      font-weight: 700;
      line-height: 170%;
      padding-top: 34px;
      padding-right: 50px;
    }
  }
  @media screen and (min-width: 768px) {
    padding-bottom: 140px;
    &__list {
      display: flex;
      margin-left: 16px;
    }
    &__element--img {
      margin-top: 38px;
    }
    &__element--data {
      font-size: $font-size-section-heading-tablet;
      line-height: 54.96px;
      padding-top: 36px;
    }
    &__element--description {
      font-size: $font-size-primary-tablet;
      line-height: 24.36px;
      padding-top: 13px;
      padding-right: 40px;
    }
  }
  @media screen and (min-width: 1280px) {
    padding-bottom: 139px;
    &__list {
      margin-left: 95px;
    }
    &__element--img {
      margin-top: 0px;
    }
    &__element--data {
      font-size: $font-size-section-heading-desktop;
      line-height: 66.41px;
      padding-top: 40px;
    }
    &__element--description {
      font-size: $font-size-primary-desktop;
      line-height: 28.16px;
      padding-top: 15px;
      padding-right: 10px;
    }
  }
}
